.NavItem {
	width: 100%;
	margin: 10px 0;
	display: block;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.NavItem a {
	display: block;
	width: 100%;
	color: #8F5C2C;
	text-decoration: none;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.NavItem a:hover,
.NavItem a:active,
.NavItem a.active {
	color: #40A4C8;
}

@media (min-width: 500px) {
	.NavItem {
		height: 100%;
		width: auto;
		margin: 0;
		display: -ms-flexbox;
		display: flex;
		-ms-flex-align: center;
		    align-items: center;
	}
	.NavItem a {
		height: 100%;
		padding: 15px 10px;
		color: white;
		border-bottom: 4px solid transparent;
	}
	.NavItem a:hover,
	.NavItem a:active,
	.NavItem a.active {
		border-bottom: 4px solid #40A4C8;
		color: white;
		background-color: #8F5C2C;
	}
}


