.Toolbar {
	width: 100%;
	height: 55px;
	position: fixed;
	top: 0;
	left: 0;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;
	z-index: 80;
	background-color: #703B09;
}

.Toolbar nav {
	height: 100%;
}

.Toolbar-logo {
	height: 100%;
	margin: 0 20px;
}

.DrawerToggle {
    width: 40px;
    height: 100%;
    margin-left: 20px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    padding: 10px 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    cursor: pointer;
}

.DrawerToggle div {
    width: 90%;
    height: 3px;
    background-color: white;
}

@media (max-width: 499px) {
	.DeskTopOnly {
		display: none;
	}
	.LogoMobile {
		height: 100%;
		margin: 0;
	}
}

@media (min-width: 500px) {
    .DrawerToggle {
        display: none;
    }
}



